@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@200..800&family=Josefin+Sans:ital,wght@0,100..700;1,100..700&family=Noto+Sans:ital,wght@0,100..900;1,100..900&display=swap');

$font-1: 'Noto Sans', cursive;
$font-2: 'Dosis', sans-serif;
$font-3: 'Josefin Sans', serif;
$color-0:rgb(43, 3, 3);
$color-1:#6c77f4;
$color-2:#6c77f4;
$color-3:#e3c415;
$color-4:black;
$color-5:#1bfff4;
$white:#ddd;
$black:#333;
$linkColor: #ffffff; // Blanc pour les liens
$borderRaduis: 25px;
$shadow: 0 1px 3px #333;
$minHeight: 95vh;

@mixin verticalCenter{
    display:flex ;
    justify-content: center;
    align-items: center;
}
*{
    box-sizing: 0;
    margin: 0;
    padding: 0;

}
body{
    margin: 0rem auto;
    max-width: 1200px;
    font-family: $font-1;
    // color: $black;
    background-color: rgb(3, 3, 55);
}
button{
    cursor: pointer;
}
li{
    list-style-type: none;
}
a{
    text-decoration: none;
    color: $black;
    cursor: pointer;
}
a:visited{
    color: $black;
}
.home, .knowledges, .portfolio, .contact{
    display: flex;

@media screen and (max-width:850px){
    display: block !important;
    
}
}
.homeContent, .knowledgesContent, .portfolioContent, .contactContent{
    margin-left: .4rem;

    @media screen and (max-width:850px){
       margin-left: 0 !important;
        
    }
}
